*{
  margin:0;
  padding: 0;
}
html,body,#app{
  width:100%;
  height:100%;
  -webkit-user-select:none;
  -webkit-text-size-adjust:100%;
  -webkit-overflow-scrolling:touch;
}
a,button,input{
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-appearance: none;
}
img,input{
  border:0;
}
